'use client';

import React, { useState, useEffect } from 'react';
import Link from 'next/link';
import { usePathname } from 'next/navigation';
import { Button } from '@/components/ui/button';
import { Menu, X, Rocket, GraduationCap, LayoutDashboard, Info, MessageSquare } from 'lucide-react';
import { motion, AnimatePresence } from 'framer-motion';

export default function Header() {
    const pathname = usePathname();
    const [isScrolled, setIsScrolled] = useState(false);
    const [isMobileMenuOpen, setIsMobileMenuOpen] = useState(false);

    useEffect(() => {
        const handleScroll = () => {
            setIsScrolled(window.scrollY > 20);
        };
        window.addEventListener('scroll', handleScroll);
        return () => window.removeEventListener('scroll', handleScroll);
    }, []);

    const navLinks = [
        { name: 'Home', href: '/', icon: GraduationCap },
        { name: 'Current CGPA', href: '/', icon: LayoutDashboard },
        { name: 'Predictive model', href: '/predict', icon: Rocket },
        { name: 'About House', href: 'https://namdapha.iitmbs.org/', icon: Info },
    ];

    return (
        <header
            className={`fixed top-0 left-0 right-0 z-50 transition-all duration-300 ${isScrolled
                    ? 'bg-white/80 backdrop-blur-lg shadow-lg py-3'
                    : 'bg-transparent py-5'
                }`}
        >
            <nav className="max-w-7xl mx-auto px-6 md:px-12 flex items-center justify-between">
                {/* Logo Area */}
                <Link href="/" className="flex items-center gap-3 group">
                    <div className="bg-primary p-2 rounded-2xl shadow-lg shadow-primary/20 group-hover:scale-110 transition-transform">
                        <GraduationCap className="w-6 h-6 text-white" />
                    </div>
                    <div>
                        <span className={`text-xl font-black tracking-tight block ${isScrolled ? 'text-slate-900' : 'text-slate-900'}`}>NAMDAPHA</span>
                        <span className="text-[10px] font-black uppercase tracking-[0.2em] text-primary block -mt-1">House Academic Hub</span>
                    </div>
                </Link>

                {/* Desktop Navigation */}
                <div className="hidden lg:flex items-center gap-8">
                    {navLinks.map((link) => {
                        const Icon = link.icon;
                        const isActive = pathname === link.href;
                        return (
                            <Link
                                key={link.name}
                                href={link.href}
                                className={`text-sm font-bold flex items-center gap-2 transition-colors hover:text-primary ${isActive ? 'text-primary' : 'text-slate-600'
                                    }`}
                            >
                                <Icon className="w-4 h-4" />
                                {link.name}
                            </Link>
                        );
                    })}
                </div>

                {/* Desktop CTA */}
                <div className="hidden lg:flex items-center gap-4">
                    <Link href="https://namdapha.iitmbs.org/">
                        <Button className="rounded-full px-6 bg-primary hover:bg-primary/90 text-white font-bold shadow-xl shadow-primary/20">
                            Join Community
                        </Button>
                    </Link>
                </div>

                {/* Mobile Toggle */}
                <button
                    className="lg:hidden p-2 text-slate-900"
                    onClick={() => setIsMobileMenuOpen(!isMobileMenuOpen)}
                >
                    {isMobileMenuOpen ? <X /> : <Menu />}
                </button>
            </nav>

            {/* Mobile Menu */}
            <AnimatePresence>
                {isMobileMenuOpen && (
                    <motion.div
                        initial={{ opacity: 0, y: -20 }}
                        animate={{ opacity: 1, y: 0 }}
                        exit={{ opacity: 0, y: -20 }}
                        className="absolute top-full left-0 right-0 bg-white shadow-2xl border-t border-slate-100 p-6 flex flex-col gap-4 lg:hidden"
                    >
                        {navLinks.map((link) => {
                            const Icon = link.icon;
                            return (
                                <Link
                                    key={link.name}
                                    href={link.href}
                                    onClick={() => setIsMobileMenuOpen(false)}
                                    className="flex items-center gap-4 p-4 rounded-2xl hover:bg-slate-50 text-slate-900 font-bold"
                                >
                                    <Icon className="w-5 h-5 text-primary" />
                                    {link.name}
                                </Link>
                            );
                        })}
                        <hr className="border-slate-100" />
                        <Link href="https://namdapha.iitmbs.org/" className="w-full">
                            <Button className="w-full rounded-2xl h-12 bg-primary text-white font-bold">
                                Join Namdapha House
                            </Button>
                        </Link>
                    </motion.div>
                )}
            </AnimatePresence>
        </header>
    );
}
